import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
import { Card, Skeleton, Switch } from 'antd';
import { useState } from 'react';
const { Meta } = Card;

function Sidebar(){
    const [loading, setLoading] = useState(true);

  const onChange = (checked) => {
    setLoading(!checked);
  };

  return (
    <div style={{marginRight:'16px',marginTop:'16px',textAlign:'right'}}>
      <Switch checked={!loading} onChange={onChange} > setting</Switch>

      <Card
        style={{
          textAlign:'center',
          width: '100%',
          marginTop: 16,
          marginRight: '16px',
        }}
        actions={[
          <SettingOutlined key="setting" />,
          <EditOutlined key="edit" />,
          <EllipsisOutlined key="ellipsis" />,
        ]}
        >
        <Skeleton loading={loading} active>
          <Meta 
            style={{textAlign:'right'}}
            title="위반사항 검출 된 것들 모아놓을 자리"
            description="위반사항 자세한 내용들 써놓을 자리"
          />
          
        </Skeleton>
      </Card>

      
      
    </div>
  );

}

export default Sidebar