import { useNavigate } from 'react-router-dom';
import { Menu,Layout, Button } from 'antd';
const { Header } = Layout;


function Headers(){
    let menu = ['Home','Progress','Start','Setting','Login']
    let navigate = useNavigate()

    const onClick = (info) => {
        navigate('/'+menu[info.key])
        
        console.log('click ', info);
      };
    
    return(
        
        <Layout>
            <Header>
                <div className="logo" />
                <Menu
                onClick={onClick}
                theme="dark"
                mode="horizontal"
                defaultSelectedKeys={['0']}
                items={menu.map((title, index) => ({
                key: String(index),
                label:title,}))}
                />
            </Header>
            
        </Layout>
        
        
    )
}

export default Headers